/* Resources */
/* Main */
body {
  font-size: 16px;
}
button, input, optgroup, select, textarea {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
}
button:focus, input:focus, optgroup:focus, select:focus, textarea:focus {
    outline: none;
}
h1, h2, h3, h4, h5 {
  font-family: "century-std", "noto-serif-patch", "source-han-serif-tc", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 3rem;
}
h1.super {
    font-size: 6rem;
}
h1.small {
    font-size: 2rem;
    font-weight: bold;
}
h1.sit {
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 0.5rem;
}
h2 {
  font-size: 2.25rem;
}
h2.small {
    font-size: 1.5rem;
    font-weight: bold;
}
hgroup > h2 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1.875rem;
}
h3.small {
    font-size: 1.25rem;
    font-weight: bold;
}
h4 {
  font-size: 1.5rem;
}
h4.small {
    font-size: 1.125rem;
    font-weight: bold;
}
h5 {
  font-size: 1.125rem;
}
h5.small {
    font-size: 1rem;
    font-weight: bold;
}
label {
  margin: 0;
}
img {
  display: block;
}
a, a:hover, a:focus, a:active, a:visited {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
a.a-text,
.a-text-only a {
  display: inline;
  border-bottom: 2px rgba(80, 227, 194, 0.5) solid;
}
a.a-text:hover, a.a-text:focus, a.a-text:active,
  .a-text-only a:hover,
  .a-text-only a:focus,
  .a-text-only a:active {
    border-bottom: 2px #50e3c2 solid;
}
a.a-block {
  display: block;
}
a.a-block .a-target {
    border-bottom: 2px rgba(80, 227, 194, 0.5) solid;
}
a.a-block:hover .a-target, a.a-block:focus .a-target, a.a-block:active .a-target {
    border-bottom: 2px #50e3c2 solid;
}
.close {
  float: none;
  display: block;
  position: absolute;
  top: -0.5rem;
  right: -2px;
  font-size: 2rem;
  line-height: 1;
  border-bottom-width: 4px;
  font-weight: normal;
  text-shadow: none;
  cursor: pointer;
}
.close, .close:hover, .close:focus {
    color: inherit;
    opacity: 1;
}
.list-unstyled {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}
.navbar,
.navbar-brand {
  margin: 0;
  padding: 0;
}
input[type='text']:not([class^="el"]),
input[type='email']:not([class^="el"]),
input[type='password']:not([class^="el"]) {
  background: none;
  border: none;
  border-bottom: 2px #9b9b9b solid;
  line-height: 1.75;
  padding: 0;
  margin: 0;
}
input[type='text']:not([class^="el"]).full-width,
  input[type='email']:not([class^="el"]).full-width,
  input[type='password']:not([class^="el"]).full-width {
    width: 100%;
}
button:not([class^="el"]):not(.btn-prev):not(.btn-next) {
  background: #b9b9b9;
  border: 2px #9b9b9b solid;
  border-radius: 2px;
  line-height: 1;
  padding: 0.75rem;
  cursor: pointer;
}
button:not([class^="el"]):not(.btn-prev):not(.btn-next).small {
    font-size: 0.875rem;
    padding: 0.5rem;
}
input + button:not([class^="el"]) {
  margin-left: 0.5rem;
}
.field {
  margin: 0.5rem 0;
}
.field:first-of-type {
    margin: 0 0 0.5rem;
}
.field:last-of-type {
    margin: 0.5rem 0 0;
}
input[type='checkbox']:not([class^="el"]) {
  -webkit-appearance: none;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  position: relative;
  -webkit-transition: .15s;
  background-color: rgba(185, 185, 185, 0.25);
  border: 2px #9b9b9b solid;
  border-radius: 2px;
}
input[type='checkbox']:not([class^="el"]):checked {
    background-color: #b9b9b9;
}
input[type='checkbox']:not([class^="el"]):checked:before {
      content: '';
      display: block;
      width: 8px;
      height: 14px;
      border-bottom: 2px black solid;
      border-right: 2px black solid;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -65%) rotate(45deg);
              transform: translate(-50%, -65%) rotate(45deg);
}
.form-check-label > * {
  vertical-align: middle;
}
.form-check-label > input + span {
  margin-left: 0.25rem;
}
.accordion > .card {
  background: none;
  border: none;
  border-radius: 0;
  padding: 0 1rem;
}
.accordion > .card > .card-head {
    padding: 1rem 0;
}
.accordion > .card > .card-body > .padding {
    padding-bottom: 1rem;
}
.modal-mask {
  display: table;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.92);
  transition: opacity .3s ease;
}
.modal-mask > .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}
.modal-mask > .modal-wrapper > .modal-dialog {
      width: 18rem;
      margin: 0 auto;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
      transition: all .3s ease;
}
.modal-enter {
  opacity: 0;
}
.modal-enter .modal-dialog {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.modal-leave-active {
  opacity: 0;
}
.modal-leave-active .modal-dialog {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.text-color-park {
  color: #FFB55D;
}
.bg-watchout {
  background-color: #50E3C2;
}
.bg-park {
  background-color: #FFB55D;
}
.bg-park-light {
  background-color: rgba(255, 181, 93, 0.25);
}
.bg-lab {
  background-color: #50E3C2;
}
.bg-lab-light {
  background-color: rgba(80, 227, 194, 0.25);
}
.bg-ask {
  background-color: #4DEBDF;
}
.bg-ask-light {
  background-color: rgba(77, 235, 223, 0.25);
}
.bg-ask-gradient-to-transparent {
  background: linear-gradient(to bottom, rgba(77, 235, 223, 0.25), rgba(77, 235, 223, 0));
}
.bg-musou {
  background-color: #FF5368;
}
.bg-musou-light {
  background-color: rgba(255, 83, 104, 0.25);
}
.bg-edu {
  background-color: #B7F661;
}
.bg-edu-light {
  background-color: rgba(183, 246, 97, 0.25);
}
.header-center {
  text-align: center;
}
.header-block {
  margin: 0;
  padding: 0.333333em 0;
}
.header-block-tall {
  padding: 1em 0;
}
section {
  width: 100%;
  padding-bottom: 2rem;
}
.container-960 {
  max-width: 990px;
  margin-left: auto;
  margin-right: auto;
}
.max-width-960 {
  max-width: 960px;
}
.logo-type {
  display: block;
  height: auto;
}
.logo-small, .icon-small {
  width: 24px;
  height: 24px;
  background-size: contain;
  background-position: center center;
}
.logo-small.musou {
  background-image: url(https://watchout.tw/asset/logo/musou/small.png);
}
.logo-small.woo {
  background-image: url(https://watchout.tw/asset/logo/woo/small.png);
}
.icon-small.transcript {
  background-image: url(https://watchout.tw/asset/icon/transcript/small.png);
}
.pgroup {
  width: 18rem;
}
.pgroup-center {
  margin: 0 auto;
}
#pitcher {
  position: fixed;
  bottom: 0.25rem;
  right: 0.25rem;
  z-index: 2;
}
#pitcher > .button {
    display: block;
    width: 5.625rem;
    height: 5.625rem;
    border-radius: 50%;
    background-size: contain;
    background-image: url(https://watchout.tw/asset/pitcher.png);
}
#pitcher > .close {
    top: auto;
    bottom: -0.25rem;
    color: #50E3C2;
    opacity: 0.75;
}
@keyframes animationFlicker {
0% {
    opacity: 1;
}
50% {
    opacity: 0.05;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes animationFlicker {
0% {
    opacity: 1;
}
50% {
    opacity: 0.05;
}
100% {
    opacity: 1;
}
}
.animate-flicker {
  -webkit-animation: animationFlicker 2s infinite;
  animation: animationFlicker 2s infinite;
}

/* Page-specific styles */
section > .label {
  font-family: "Space Mono", "noto-serif-patch", "source-han-serif-tc", monospace;
  position: relative;
  height: 3rem;
  margin: 0 auto;
  padding: 0;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 3rem;
  display: block;
  text-align: center;
}
section > .label > .lightbulb {
    position: relative;
    width: 1.5rem;
    height: 3rem;
    margin-right: 0.25rem;
}
section > .label > .lightbulb > .bulb {
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      left: 0;
      border-radius: 50%;
      border: 2px #FF5368 solid;
}
section > .label[status="LIVE"] > .lightbulb > .bulb {
    background: #FF5368;
}
section > .label[status="SOON"] > .lightbulb > .bulb {
    border: 4px #FF5368 solid;
}
section#cover {
  height: 0;
  padding-bottom: 88.8888889%;
  background-color: rgba(0, 0, 0, 0.65);
  background-size: cover;
  background-position: center center;
  background-image: url(~assets/opening-compact.jpg);
}
@media (min-width: 576px) {
section#cover {
      padding-bottom: 50%;
      background-image: url(~assets/opening-regular.jpg);
}
}
@media (min-width: 992px) {
section#cover {
      padding-bottom: 33.333333%;
      background-image: url(~assets/opening-wide.jpg);
}
}
@media (min-width: 576px) {
section#now {
    padding-top: 2rem;
}
section#now > .label {
      width: 6rem;
}
section#now > .label:before, section#now > .label:after {
        position: absolute;
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
}
section#now > .label:before {
        border-bottom: 3rem rgba(255, 83, 104, 0.25) solid;
        border-left: 1.73205rem transparent solid;
        right: 100%;
}
section#now > .label:after {
        border-top: 3rem rgba(255, 83, 104, 0.25) solid;
        border-right: 1.73205rem transparent solid;
        left: 100%;
}
}
.player-container {
  width: 100%;
}
.player-container > .player {
    position: relative;
    max-width: 60rem;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.65);
    background-size: cover;
    background-position: center center;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
}
.player-container > .player > .embed-responsive-item {
      background-size: contain;
}
.player-container > .player > .play {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      width: 96px;
      height: 96px;
      background-image: url(../assets/play.png);
      background-size: contain;
      cursor: pointer;
}
@media (min-width: 576px) {
.player-container > .player > .play {
          width: 192px;
          height: 192px;
}
}
.organizers > ul > label {
  font-size: 0.875rem;
  font-weight: 500;
}
.date, .time {
  display: block;
}
.date {
  font-family: "Space Mono", monospace;
}
.date > .date-separator {
    color: #4DEBDF;
}
.date.date-large {
    font-size: 1.5rem;
    line-height: 1.875rem;
}
.date.date-large > .date-separator {
      font-size: 1rem;
      line-height: 1.875rem;
}
.time {
  font-family: "Space Mono", monospace;
}
.time.time-large {
    font-size: 1.5rem;
    line-height: 1.875rem;
}
.event {
  position: relative;
}
.event .info {
    margin: 0.5rem 0;
}
.event .guests {
    margin: 0 -0.5rem;
}
.event .organizers {
    margin: 0.5rem 0;
}
.guest {
  position: relative;
  margin: 0.5rem;
}
.guest > .photo {
    width: 8rem;
    height: 0;
    padding-bottom: 100%;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.65);
    background-size: cover;
}
.guest > .name, .guest > .job {
    display: block;
}
.guest > .job {
    font-size: 0.875rem;
    font-weight: 500;
}
.guest.photo-small > .photo {
    width: 6rem;
}
.guest.photo-large > .photo {
    width: 12rem;
}
.guest.signup > .photo {
    background-color: #4DEBDF;
    background-image: url(../assets/+1.png);
    background-size: contain;
    background-position: center center;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
}
.guest.signup.online > .photo {
    background-image: url(../assets/+1-online.png);
}
.event-with-player h2, .event-with-signup h2 {
  margin: 1rem 0;
}
.event-with-signup .event-text {
  margin: 0 1rem;
  max-width: 18rem;
}
.event-with-signup .event-images {
  margin: 1rem;
  max-width: 36rem;
}
.event-in-list {
  border-top: 4px rgba(77, 235, 223, 0.85) solid;
  margin-bottom: 2rem;
}
.event-in-list .title {
    margin: 0;
}
.event-in-list .guest {
    margin: 0.25rem 0.25rem;
}
.event-in-list .name {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 0.875rem;
    text-align: right;
    background: white;
    padding: 0 0.25rem;
}
.event-in-list .job {
    display: none;
}
.event-in-list .links > .link {
    margin-right: 0.5rem;
}
.event-in-list .links > .link > .logo-small, .event-in-list .links > .link > .icon-small, .event-in-list .links > .link > .label {
      display: inline-block;
      vertical-align: top;
}
.event-in-list .links > .link > .logo-small, .event-in-list .links > .link > .icon-small {
      margin-right: 0.25rem;
}
.event-in-list .links.contained {
    position: absolute;
    bottom: 0;
}
.event-in-list.guest-one {
    padding-top: 1.5rem;
}
.event-in-list.guest-one .info {
      position: absolute;
      top: 0;
      z-index: 1;
}



